import React from 'react';
import './css/index.scss';

interface myProps {
  scrollLength?: Number
}
interface myState {

}

class Head extends React.Component <myProps, myState> {
  constructor (props: myProps) {
    console.log(props);
    super(props);
    this.state = {
    }
  }
  render () {
    const { scrollLength } = this.props;
    return (
      <>
       <header
        style={{
          position: scrollLength >= 90 ? 'fixed' : 'absolute',
          backgroundColor: scrollLength >= 90 ? 'rgba(255, 255, 255, 0.3)' : 'rgba(255, 255, 255, 1)',
        }}
       >{ '当前滚动距离：' + scrollLength }</header>
      </>
    )
  }
}

export default Head;