import React from 'react';
import Head from '../../components/head/index';
import './css/index.scss';

interface myProps {
}

interface myState {
  scrollTop: Number
}

class Page extends React.Component <myProps, myState> {
  constructor (props: myProps) {
    super(props);
    this.state = {
      scrollTop: 0,
    }
  }
  // static defaultProps = {
  //   scrollLength: 0,
  // };
  componentDidMount () {
    let that = this;
    that.setState({
      scrollTop: document.documentElement.scrollTop
    });
    window.addEventListener('scroll', () => {
      that.setState({
        scrollTop: document.documentElement.scrollTop
      });
    })
  }
  render() {
    return (
      <>
        <div className="page">
          <Head scrollLength={this.state.scrollTop} />
          <div className="content">{ this.state.scrollTop }</div>
        </div>
      </>
    )
  }
}

export default Page;